"use client"

import { useState, useEffect } from "react"
import { useRouter, useSearchParams } from "next/navigation"
import Link from "next/link"
import { Eye, EyeOff, LogIn, User, Lock, AlertCircle, Mail } from "lucide-react"
import Image from "next/image"

export default function LoginPage() {
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [showPassword, setShowPassword] = useState(false)
  const [rememberMe, setRememberMe] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState("")
  
  const router = useRouter()
  const searchParams = useSearchParams()
  const callbackUrl = searchParams.get('callbackUrl') || "/"

  // 检查是否已登录
  useEffect(() => {
    const token = localStorage.getItem('auth_token')
    if (token) {
      router.push(callbackUrl)
    }
  }, [router, callbackUrl])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError("")
    setIsLoading(true)

    try {
      // 模拟API调用
      await new Promise(resolve => setTimeout(resolve, 1000))

      // 模拟验证
      if (!email || !password) {
        throw new Error("请填写所有必填字段")
      }

      if (!email.includes("@")) {
        throw new Error("请输入有效的邮箱地址")
      }

      // 模拟成功登录
      const mockUser = {
        id: "12345",
        name: email.split("@")[0],
        email: email,
        avatar: null,
        createdAt: new Date().toISOString()
      }

      const mockToken = "mock_jwt_token_" + Math.random().toString(36).substring(7)

      // 保存到localStorage
      localStorage.setItem('auth_token', mockToken)
      localStorage.setItem('user_info', JSON.stringify(mockUser))
      
      // 如果是"记住我"，保存到sessionStorage以便刷新后保持登录
      if (rememberMe) {
        sessionStorage.setItem('remembered_auth', 'true')
      }

      // 触发storage事件以更新导航栏状态
      window.dispatchEvent(new Event('storage'))

      // 获取回调URL或跳转到首页
      const savedCallback = localStorage.getItem('login_callback') || callbackUrl
      localStorage.removeItem('login_callback')
      
      router.push(savedCallback)

    } catch (err) {
      setError(err instanceof Error ? err.message : "登录失败，请稍后重试")
    } finally {
      setIsLoading(false)
    }
  }

  const handleSocialLogin = (provider: string) => {
    alert(`即将通过${provider}登录，此功能待实现`)
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-indigo-50 to-purple-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
      <div className="sm:mx-auto sm:w-full sm:max-w-md">
        <div className="flex justify-center">
          <Link href="/" className="flex items-center gap-2 hover:opacity-80 transition-opacity">
            <div className="relative w-40 h-16">
              <Image
                src="/images/e4-b8-9c-e7-9b-9f-e4-b8-87-e8-b1-a12.png"
                alt="东盟万象"
                fill
                className="object-contain"
                priority
              />
            </div>
          </Link>
        </div>
        <h2 className="mt-6 text-center text-3xl font-bold text-[#002760]">
          欢迎回来
        </h2>
        <p className="mt-2 text-center text-sm text-gray-600">
          登录您的账户，探索东盟文化之美
        </p>
      </div>

      <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
        <div className="bg-white py-8 px-4 shadow-2xl shadow-blue-100/50 sm:rounded-2xl sm:px-10 border border-blue-100">
          {error && (
            <div className="mb-6 bg-red-50 border border-red-200 text-red-600 px-4 py-3 rounded-lg flex items-center gap-2">
              <AlertCircle size={20} />
              <span className="text-sm">{error}</span>
            </div>
          )}

          <form onSubmit={handleSubmit} className="space-y-6">
            <div>
              <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-2">
                邮箱地址
              </label>
              <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Mail size={20} className="text-gray-400" />
                </div>
                <input
                  id="email"
                  name="email"
                  type="email"
                  autoComplete="email"
                  required
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  className="appearance-none block w-full pl-12 pr-3 py-3 border border-gray-300 rounded-xl placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-[#002760] focus:border-transparent transition-all text-gray-900 bg-gray-50 focus:bg-white"
                  placeholder="请输入您的邮箱"
                />
              </div>
            </div>

            <div>
              <label htmlFor="password" className="block text-sm font-medium text-gray-700 mb-2">
                密码
              </label>
              <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Lock size={20} className="text-gray-400" />
                </div>
                <input
                  id="password"
                  name="password"
                  type={showPassword ? "text" : "password"}
                  autoComplete="current-password"
                  required
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="appearance-none block w-full pl-12 pr-12 py-3 border border-gray-300 rounded-xl placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-[#002760] focus:border-transparent transition-all text-gray-900 bg-gray-50 focus:bg-white"
                  placeholder="请输入您的密码"
                />
                <button
                  type="button"
                  onClick={() => setShowPassword(!showPassword)}
                  className="absolute inset-y-0 right-0 pr-3 flex items-center"
                >
                  {showPassword ? (
                    <EyeOff size={20} className="text-gray-400 hover:text-gray-600" />
                  ) : (
                    <Eye size={20} className="text-gray-400 hover:text-gray-600" />
                  )}
                </button>
              </div>
            </div>

            <div className="flex items-center justify-between">
              <div className="flex items-center">
                <input
                  id="remember-me"
                  name="remember-me"
                  type="checkbox"
                  checked={rememberMe}
                  onChange={(e) => setRememberMe(e.target.checked)}
                  className="h-4 w-4 text-[#002760] focus:ring-[#002760] border-gray-300 rounded"
                />
                <label htmlFor="remember-me" className="ml-2 block text-sm text-gray-700">
                  记住我
                </label>
              </div>

              <div className="text-sm">
                <Link
                  href="/auth/forgot-password"
                  className="font-medium text-[#002760] hover:text-[#001d47] transition-colors"
                >
                  忘记密码?
                </Link>
              </div>
            </div>

            <div>
              <button
                type="submit"
                disabled={isLoading}
                className="group relative w-full flex justify-center py-3 px-4 border border-transparent rounded-xl text-sm font-bold text-white bg-[#002760] hover:bg-[#001d47] focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-[#002760] disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-200 shadow-lg hover:shadow-xl"
              >
                <span className="absolute left-0 inset-y-0 flex items-center pl-3">
                  <LogIn size={20} className="text-yellow-400" />
                </span>
                {isLoading ? "登录中..." : "登录"}
              </button>
            </div>
          </form>

          <div className="mt-6">
            <div className="relative">
              <div className="absolute inset-0 flex items-center">
                <div className="w-full border-t border-gray-300" />
              </div>
              <div className="relative flex justify-center text-sm">
                <span className="px-2 bg-white text-gray-500">其他登录方式</span>
              </div>
            </div>

            <div className="mt-6 grid grid-cols-2 gap-3">
              <button
                onClick={() => handleSocialLogin("微信")}
                className="w-full inline-flex justify-center items-center py-2.5 px-4 border border-gray-300 rounded-xl shadow-sm bg-white text-sm font-medium text-gray-700 hover:bg-gray-50 transition-colors"
              >
                <div className="w-5 h-5 mr-2 relative">
                  <Image
                    src="https://img.icons8.com/color/48/000000/wechat.png"
                    alt="微信"
                    fill
                    className="object-contain"
                  />
                </div>
                微信
              </button>
              <button
                onClick={() => handleSocialLogin("Google")}
                className="w-full inline-flex justify-center items-center py-2.5 px-4 border border-gray-300 rounded-xl shadow-sm bg-white text-sm font-medium text-gray-700 hover:bg-gray-50 transition-colors"
              >
                <div className="w-5 h-5 mr-2 relative">
                  <Image
                    src="https://img.icons8.com/color/48/000000/google-logo.png"
                    alt="Google"
                    fill
                    className="object-contain"
                  />
                </div>
                Google
              </button>
            </div>
          </div>

          <div className="mt-8 text-center">
            <p className="text-sm text-gray-600">
              还没有账户?{' '}
              <Link
                href="/auth/register"
                className="font-medium text-[#002760] hover:text-[#001d47] transition-colors"
              >
                立即注册
              </Link>
            </p>
          </div>

          <div className="mt-6 text-center text-xs text-gray-500">
            <p>登录即表示您同意我们的</p>
            <p>
              <Link href="/terms" className="text-[#002760] hover:underline">服务条款</Link>
              {' '}和{' '}
              <Link href="/privacy" className="text-[#002760] hover:underline">隐私政策</Link>
            </p>
          </div>
        </div>
      </div>
    </div>
  )
}